body {
  font-family: sans-serif;
  color: #333;
}

#app {
  position: relative;
  z-index: 0;
}

.DraftEditor-editorContainer [data-contents='true'] > div {
  margin: 10px 0;
}

.DraftEditor-editorContainer [data-contents='true'] > figure {
  margin: 10px 0;
}

.DraftEditor-editorContainer [data-contents='true'] > figure > hr {
  margin: 20px 0;
  border: none;
  height: 2px;
  background-color: #ddd;
}

.DraftEditor-editorContainer [data-contents='true'] > pre > pre > .code-block {
  position: relative;
  padding: 10px;
  tab-size: 4;
  background-color: #eee;
}

.DraftEditor-editorContainer
  [data-contents='true']
  > pre
  > pre
  > .code-block
  .code-block-copy-button {
  position: absolute;
  top: 0;
  right: 0;
}

.DraftEditor-editorContainer
  [data-contents='true']
  > pre
  > pre
  > .code-block
  .code-block-copy-button::before {
  content: 'Copy';
  display: block;
  font-size: 13px;
  color: #222;
}

.DraftEditor-editorContainer
  [data-contents='true']
  > pre
  > pre
  > .code-block.code-block-copied
  .code-block-copy-button::before {
  content: 'Copied';
}

.DraftEditor-editorContainer [data-contents='true'] > blockquote {
  margin: 10px 0;
  padding: 5px 10px;
  border-left: solid 4px #eee;
}

.DraftEditor-editorContainer [data-contents='true'] > .checkable-list {
  list-style: none;
}

.DraftEditor-editorContainer [data-contents='true'] .checkable-list-item {
  position: relative;
  transform: translateX(-1.5em);
  z-index: 0;
}

.DraftEditor-editorContainer
  [data-contents='true']
  .checkable-list-item-checkbox {
  position: absolute;
  z-index: 1;
}

.DraftEditor-editorContainer
  [data-contents='true']
  .checkable-list-item-content {
  padding-left: 1.5em;
}

.DraftEditor-editorContainer [data-contents='true'] code {
  padding: 0 4px;
  border-radius: 4px;
  background-color: #eee;
}

.DraftEditor-editorContainer [data-contents='true'] a {
  color: #3366d6;
  text-decoration: none;
}

.DraftEditor-editorContainer [data-contents='true'] a:hover {
  text-decoration: underline;
}

/* GitHub corner */

.github-corner {
  position: absolute;
  z-index: 1;
  top: 0;
  right: 0;
  border: none;
  fill: #151513;
  color: #fff;
}
